.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  background: #f5f5f5;
}

/* LEFT */
.header-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.account-title {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}

/* RIGHT */
.header-right {
  display: flex;
  align-items: center;
  gap: 14px;
}

.icon {
  font-size: 18px;
  cursor: pointer;
}

/* PDF badge style */
.pdf {
  font-size: 13px;
  padding: 2px 6px;
  font-weight: 600;
}


/* =========================
   MOBILE HEADER FIX
========================= */
@media (max-width: 768px) {
  .app-header {
    height: 56px;
    padding: 0 12px;
  }

  .header-left,
  .header-right {
    gap: 12px;
  }

  .header-icon {
    font-size: 24px;
    padding: 6px;
  }

  .account-title {
    font-size: 16px;
    max-width: 160px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

